import { cilPencil, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import { CButton, CCard, CCardBody, CCol, CModal, CModalBody, CModalFooter, CModalHeader, CModalTitle, CRow, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify'
import './Media.scss';

const MediaList = () => {
  const [brand, setBrand] = useState([]);
  const [updateList, setUpdateList] = useState({});
  const [media, setMedia] = useState([])  

  useEffect(() => {
      let url =  process.env.REACT_APP_API_URL+"images/list"
      fetch(url, {
        method: 'POST',
        redirect: 'follow'
      })
      .then(response => response.json())
      .then(result => { console.log(result.data); setMedia(result.data) })
      .catch(error => console.log('error', error));

  }, []);

  const handleDelete =(arg)=>{
    let requestOptions = {
      method: 'POST',
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        "_id": arg
      }),
    };
    const confirmWrp =  confirm("Are you sure you want to delete this?");
    if(!confirmWrp){
      return;
    }
    fetch(process.env.REACT_APP_API_URL+"images/list", requestOptions)
    .then(response => response.json())
    .then(result => {
      if(result.status==200){
        toast.success(result.message);
        // setUpdateList({"_id": arg});       
      }else{
        toast.error(result.message);
      }
    })
    .catch(error => console.log('error', error)); 
  }

  const [visible, setVisible] = useState(false)

  const openFullImage = (arg)=>{
    setVisible(!visible)
  }

  return (
    <>
    <ToastContainer />
      <div className='d-flex justify-content-between  align-items-center  mb-4'>
        <h1 className='headingType1'>Media</h1>
        <Link to={'/brands/add'} className='btn btn-primary'>Add</Link>
      </div>
      <CCard>
        <CCardBody>
          <CRow>
            <CCol lg>
              <CRow className='sliderInfoWrapper'>
                {media.map((item, key)=>(
                  <CCol lg={2} key={key}>
                    <div className='each_image_wrp' onClick={()=>openFullImage(item._id)}>
                      <img src={process.env.REACT_APP_BASE_URL+item.image}  alt={item.alt_text} width={'100%'}/>
                    </div>
                  </CCol>
                ))}            
              </CRow>
            </CCol>            
          </CRow>
          
        </CCardBody>
      </CCard>

     
      <CModal size='xl' visible={visible} onClose={() => setVisible(false)}>
        <CModalHeader onClose={() => setVisible(false)}>
          <CModalTitle>Modal title</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <p>Woohoo, you're reading this text in a modal!</p>
        </CModalBody>
        <CModalFooter>
          <CButton color="secondary" onClick={() => setVisible(false)}>
            Close
          </CButton>
          <CButton color="primary">Save changes</CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}

export default MediaList
